import qrcode from '../../assets/img/qr-code.png';
import './rightStyled.css'
export default function RightSide(){
    return(
        <>
            <div className="right">
                <img src={qrcode} alt="Imagem QRCode"/>
                <h2>Entrar Com o Cdigo QR</h2>
                <p><strong>Escaneie isso com o app movel do DeskCode</strong> para fazer login imediatamente</p>
             </div>
        </>
    )
}